import React from 'react';
import { Link } from 'react-router-dom';
import _ from 'lodash';
import { Row, Col } from 'reactstrap';
import cons from '../../../constants'
import './Notification.scss';
import { getUserFullName, getModalUserFullName, getDateTime, splitContent, getDateFormat, getTime } from '../../../helpers/UtilsHelper';

const Notification = (props) => {
    const { notification, updateNotification } = props
    const timeStamp = getDateTime(notification.createdAt)
    const notificationTypeInfo = !_.isNull(notification) && cons.NOTIFICATION_TYPES_INFO.find(elm => elm.id === Number(notification.notificationType))
    const type = notificationTypeInfo ? notificationTypeInfo.contentType : 'Unknown'
    return (
        <div className="ml-3 my-3 notification">
            <Row >
                <div>
                    {!notification.read ?
                        <i className="fas fa-bell cursor-pointer" onClick={() => updateNotification(notification.id, true)} />
                        : <i className="far fa-bell" title="Bookmark unread" onClick={() => updateNotification(notification.id, false)} />
                    }
                </div>
                <Col> <RenderContent type={type} notification={notification} updateNotification={updateNotification} /></Col>
            </Row>
            <div className="text-right notification__timestamp">
                <span>{timeStamp}</span>
            </div>
        </div>
    );
}

const RenderContent = (props) => {
    const { type, notification, updateNotification } = props;
    switch (Number(notification.notificationType)) {
        case cons.NOTIFICATION_TYPES.ADMIN_ADD_NEW_POST:
            return <Community
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />
        case cons.NOTIFICATION_TYPES.MESSAGE_FROM_ADMIN:
            return <Community
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        case cons.NOTIFICATION_TYPES.UPCOMING_BOOKING_REMINDER:
            return <UpcomingBooking
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        case cons.NOTIFICATION_TYPES.UPCOMING_BOOKING_REMINDER_OLD:
            return <UpcomingBooking
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        case cons.NOTIFICATION_TYPES.CLIENT_MESSAGES:
            return <Message
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        case cons.NOTIFICATION_TYPES.ADMIN_EDIT_BOOKING:
            return <AdminEditBooking
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        case cons.NOTIFICATION_TYPES.CHANGE_MAKE_BY_ADMIN:
            return <ChangeMakeByAdmin
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        case cons.NOTIFICATION_TYPES.CLIENT_CHECK_IN:
            return <ClientCheckIn
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />
        case cons.NOTIFICATION_TYPES.BOOKING_CREATED:
            return <BookingCreated
                type={type}
                notification={notification}
                updateNotification={updateNotification}
            />

        default:
            return <ManualNotification
                type={type}
                notification={notification}
            />
    }
}

const Community = (props) => {
    const { type, notification, updateNotification } = props;
    const content = JSON.parse(notification.content)
    const message = _.get(content, "content", "")
    const displayMessage = message && splitContent(message);
    return (
        <Link to={"/provider/community"} onClick={() => updateNotification(notification.id, true)}>
            <h4 className={!notification.read ? "active-message" : "inactive-message"}><span>{type}</span> - {displayMessage}</h4>
        </Link>
    )
}

const UpcomingBooking = (props) => {
    const { type, notification, updateNotification } = props;
    const content = JSON.parse(notification.content)
    const { start_date, start_time, end_time, space = {} } = content
    const spaceName = _.get(space, "name", "Non-Ethera")
    const venueName = _.get(space, "venue.name", "")
    return (
        <Link to={"/provider/bookings"} onClick={() => updateNotification(notification.id, true)}>
            <h4 className={!notification.read ? "active-message" : "inactive-message"}>
                <span>{type}</span> - Upcoming Booking on
                <span> {getDateFormat(start_date)}</span> from
                <span> {getTime(`${start_date} ${start_time}`)}</span> to
                <span> {getTime(`${start_date} ${end_time}`)}</span> at
                <span> {spaceName}</span> {space ? "in" : ""}
                {space && <span> {venueName}</span>}
            </h4>
        </Link>
    )
}

const Message = (props) => {
    const { type, notification, updateNotification } = props;
    const content = JSON.parse(notification.content)
    const clientName = content && getModalUserFullName(content.client)
    return (
        <Link to={{
            pathname: `/provider/clients/${content.client.id}`,
            state: {
                goToMessagesPage: true
            }
        }} onClick={() => updateNotification(notification.id, true)}>
            <h4 className={!notification.read ? "active-message" : "inactive-message"}><span>{type}</span> - Message received from <span >{clientName}</span></h4>
        </Link>
    )
}

const AdminEditBooking = (props) => {
    const { type, notification, updateNotification } = props;
    return (
        <Link to={"/provider/bookings"} onClick={() => updateNotification(notification.id, true)}>
            <h4 className={!notification.read ? "active-message" : "inactive-message"}>
                <span>{type}</span>
            </h4>
        </Link>
    )
}

const getSpaceName = (space) => {
    if (!space || !space.name) {
        return "Alternate space";
    }
    else {
        const { venue } = space;
        return `${space.name} | ${venue.name}`;
    }
}

const ChangeMakeByAdmin = (props) => {
    const { type, notification, updateNotification } = props;
    const content = JSON.parse(notification.content)
    const { start_date, start_time } = content
    return (
        <h4 className={`cursor-pointer ${!notification.read ? "active-message" : "inactive-message"}`} onClick={() => updateNotification(notification.id, true)}>
            <span>{type} </span>
            - There has been a change to one of your bookings
            (on <span>{getDateFormat(start_date)}</span> from <span>{getTime(`${start_date} ${start_time}`)}</span> at <span>{getSpaceName(content.space)}</span>),
            please contact the Admin for more information.
        </h4>
    )
}

const ClientCheckIn = (props) => {
    const { type, notification, updateNotification } = props;
    const content = JSON.parse(notification.content)
    const { start_time } = content
    const clientName = content && getUserFullName(content.client)
    return (
        <h4 className={`cursor-pointer ${!notification.read ? "active-message" : "inactive-message"}`} onClick={() => updateNotification(notification.id, true)}>
            <span>{type} </span>
            - <span>{clientName}</span> has checked in for their appointment at <span>{start_time}</span>
        </h4>
    )
}

const ManualNotification = (props) => {
    const { type, notification } = props;
    return <h4 className={!notification.read ? "active-message" : "inactive-message"}><span>{type}</span></h4>
}


const BookingCreated = (props) => {
    const { type, notification, updateNotification } = props;
    const content = JSON.parse(notification.content)
    const { start_time, start_date } = content
    const spaceName = getSpaceName(content.space)
    return (
        <h4 className={`cursor-pointer ${!notification.read ? "active-message" : "inactive-message"}`} onClick={() => updateNotification(notification.id, true)}>
            <span>{type} </span>
            - on <span>{getDateTime(`${start_date} ${start_time}`)}</span> at <span>{spaceName}</span>
        </h4>
    )
}

export default Notification;