import React from "react";
import { Button, Modal, Label, Input } from "reactstrap";

export default function NotificationSettingModal(props) {
  const {
    isOpen,
    toggle,
    handle,
    notificationSetting,
    handleMultipleSelectNotificationTypes,
  } = props;
  return (
    <Modal className="modal-dialog-centered" isOpen={isOpen} toggle={toggle}>
      <div className="modal-header">
        <h6 className="modal-title" id="modal-title-default">
          Notification Settings
        </h6>
        <button
          aria-label="Close"
          className="close"
          data-dismiss="modal"
          type="button"
          onClick={toggle}
        >
          <span aria-hidden={true}>Ã—</span>
        </button>
      </div>

      <div className="modal-body text-center">
        <Label className="form-control-label mb-3">
          Select the type of notifications displayed
        </Label>
        <div className="mx-3">
          <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="clientMessages"
              id="clientMessages"
              checked={notificationSetting.clientMessages}
              onChange={(e) =>
                handleMultipleSelectNotificationTypes(e, notificationSetting)
              }
            />
            <Label className="custom-control-label" htmlFor="clientMessages">
              Messages from client
            </Label>
          </div>

          {/* <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="bookingCreated"
              id="bookingCreated"
              checked={notificationSetting.bookingCreated}
              onChange={(e) => handleMultipleSelectNotificationTypes(e, notificationSetting)}
            />
            <Label className="custom-control-label" htmlFor="bookingCreated">
              Booking created
              </Label>
          </div> */}

          <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="upcomingBookingReminder"
              id="upcomingBookingReminder"
              checked={notificationSetting.upcomingBookingReminder}
              onChange={(e) =>
                handleMultipleSelectNotificationTypes(e, notificationSetting)
              }
            />
            <Label
              className="custom-control-label"
              htmlFor="upcomingBookingReminder"
            >
              Upcoming booking reminder
            </Label>
          </div>

          <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="upcomingClientAppointment"
              id="upcomingClientAppointment"
              checked={notificationSetting.upcomingClientAppointment}
              onChange={(e) =>
                handleMultipleSelectNotificationTypes(e, notificationSetting)
              }
            />
            <Label
              className="custom-control-label"
              htmlFor="upcomingClientAppointment"
            >
              Upcoming client appointment
            </Label>
          </div>

          <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="changeMakeByAdmin"
              id="changeMakeByAdmin"
              checked={notificationSetting.changeMakeByAdmin}
              onChange={(e) =>
                handleMultipleSelectNotificationTypes(e, notificationSetting)
              }
            />
            <Label className="custom-control-label" htmlFor="changeMakeByAdmin">
              Changes made by admin
            </Label>
          </div>

          <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="messageFromAdmin"
              id="messageFromAdmin"
              checked={notificationSetting.messageFromAdmin}
              onChange={(e) =>
                handleMultipleSelectNotificationTypes(e, notificationSetting)
              }
            />
            <Label className="custom-control-label" htmlFor="messageFromAdmin">
              Admin adds new post
            </Label>
          </div>

          <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="clientCheckIn"
              id="clientCheckIn"
              checked={notificationSetting.clientCheckIn}
              onChange={(e) =>
                handleMultipleSelectNotificationTypes(e, notificationSetting)
              }
            />
            <Label className="custom-control-label" htmlFor="clientCheckIn">
              Client check in
            </Label>
          </div>

          {/* <div className="custom-control custom-checkbox text-left mb-3">
            <Input
              className="custom-control-input"
              type="checkbox"
              name="clientAppointmentStart"
              id="clientAppointmentStart"
              checked={notificationSetting.clientAppointmentStart}
              onChange={(e) => handleMultipleSelectNotificationTypes(e, notificationSetting)}
            />
            <Label className="custom-control-label" htmlFor="clientAppointmentStart">
              Client appointment start
              </Label>
          </div> */}
        </div>
      </div>
      <div className="modal-footer">
        <Button
          color="danger"
          outline
          data-dismiss="modal"
          type="button"
          onClick={toggle}
        >
          Cancel
        </Button>
        <Button
          className="ml-auto"
          color="success"
          type="button"
          onClick={handle}
        >
          Save
        </Button>
      </div>
    </Modal>
  );
}
