import React from "react";
import _ from "lodash";
import { useDispatch, useSelector } from "react-redux";
import {
  Container,
  Card,
  CardHeader,
  CardBody,
  Row,
  Col,
  Button,
} from "reactstrap";
import {
  getListNotification,
  updateNotification,
  markAllReadNotification,
} from "../../../redux/actions/notificationAction";
import Notification from "./Notification";
import "./ListNotification.scss";
import NotificationSettingModal from "components/Modal/NotificationSettingModal";
import notificationSettingApi from "../../../api/notificationSettingApi";
import Loading from "../../../components/Loading";
import { toast } from "react-toastify";
import { useState } from "react";
import { useEffect } from "react";

export default function ListNotification(props) {
  const dispatch = useDispatch();
  const notificationData = useSelector((state) => state.notification.data);
  const loading = useSelector((state) => state.notification.loading);
  const [isShowSettingModal, setIsShowSettingModal] = useState(false);
  const toggleSettingNotification = () => {
    setIsShowSettingModal(!isShowSettingModal);
  };
  const [notificationSetting, setNotificationSetting] = useState({});

  useEffect(() => {
    dispatch(getListNotification());
    notificationSettingApi
      .getNotificationSetting()
      .then((res) => {
        setNotificationSetting(res);
      })
      .catch((err) => {
        toast.error("get fail");
      });
  }, []);

  const handleUpdateNotification = (id, value) => {
    let data = { read: value };
    dispatch(updateNotification(id, data));
  };

  const markAllRead = (listNotification) => {
    let unReadNotifications = listNotification.filter((item) => !item.read);
    !_.isNull(unReadNotifications) &&
      unReadNotifications.forEach((elm) => {
        dispatch(markAllReadNotification(elm.id, { read: true }));
      });
  };

  const handleMultipleSelectNotificationTypes = (e, obj) => {
    const { checked, name } = e.target;
    obj[name] = checked;
    setNotificationSetting({ ...obj });
  };

  const applyNotificationSetting = () => {
    const {
      bookingCreated,
      clientMessages,
      upcomingBookingReminder,
      upcomingClientAppointment,
      changeMakeByAdmin,
      messageFromAdmin,
      clientCheckIn,
      clientAppointmentStart,
    } = notificationSetting;
    const data = {
      bookingCreated,
      clientMessages,
      upcomingBookingReminder,
      upcomingClientAppointment,
      changeMakeByAdmin,
      messageFromAdmin,
      clientCheckIn,
      clientAppointmentStart,
    };
    notificationSettingApi
      .updateNotificationSetting(data)
      .then((res) => {
        setNotificationSetting(res);
        toast.success("Update notification setting success");
        toggleSettingNotification();
      })
      .catch((err) => {
        console.log(err);
        toast.error("Update notification setting fail");
      });
  };

  const listNotification = notificationData.result;

  return (
    <Container className="list-notification">
      {loading && <Loading />}
      <NotificationSettingModal
        isOpen={isShowSettingModal}
        toggle={toggleSettingNotification}
        handle={applyNotificationSetting}
        notificationSetting={notificationSetting}
        handleMultipleSelectNotificationTypes={
          handleMultipleSelectNotificationTypes
        }
      />
      <Card className="my-3">
        <CardHeader className="ml-3">
          <Row>
            <Col>
              <h1>
                <b>Notifications</b>
              </h1>
            </Col>
            <Col className="text-center">
              <Button
                color="link"
                onClick={() => markAllRead(listNotification)}
              >
                <i className="fas fa-check-double fa-sm" /> Mark all as read{" "}
              </Button>
            </Col>
            <Col className="text-right">
              <Button color="link" onClick={toggleSettingNotification}>
                <i className="fas fa-cog fa-lg" />
              </Button>
            </Col>
          </Row>
        </CardHeader>
        <CardBody>
          {listNotification &&
            listNotification.map((item) => {
              return (
                <Notification
                  key={item.id}
                  notification={item}
                  updateNotification={handleUpdateNotification}
                />
              );
            })}
        </CardBody>
      </Card>
    </Container>
  );
}
